<template>
  <div class="subject_list">
    <head-top title="我的课程">
      <router-link slot="right" to="/subject/edit">
        <van-icon name="plus" class="head-right"></van-icon>
      </router-link>
    </head-top>

    <!--    <div class="search_input">-->
    <!--      <van-search placeholder="请输入搜索关键词" v-model="value" @search="onSearch"/>-->
    <!--    </div>-->

    <van-pull-refresh v-model="isLoading" @refresh="onSearch" class="pull_fresh">
      <van-cell-group class="cell_group" v-if="dataList && dataList.length>0">
        <van-cell class="cell_main" v-for="(item, index) in dataList" :key="index" is-link @click="courseClick(item)">
          <template slot="icon">
            <!--            <img :src="item.imgSrc" alt=""/>-->
            <van-icon class="cell_icon" class-prefix="iconfont icon-kecheng-weixuanzhong"></van-icon>
          </template>
          <template slot="title">
            <div class="line_title">
              {{item.name}}
              <van-icon class="operate" name="edit" @click.stop.prevent="toEdit(item)"></van-icon>
            </div>
          </template>
          <template slot="label">
            <div class="line_title">{{item.remark}}</div>
          </template>
        </van-cell>
      </van-cell-group>
      <r-lack-data v-else></r-lack-data>
    </van-pull-refresh>
  </div>
</template>

<script>
import defaultImg from "../../../assets/boy_img.png";
import { formatDate } from "@/utils/formatData";

export default {
  name: "subject-list",
  data () {
    return {
      dataList: [],
      isLoading: false,
      value: ""
    };
  },
  mounted () {
    this.onSearch();
  },
  methods: {
    onSearch () {
      this.dataList = [];
      this.isLoading = true;
      this.$mainHttp.course.dataList({
        userID: this.$store.getters.userID
      }).then(res => {
        if (res.Result) {
          res.Datas.sort((a, b) => {
            return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
          });

          res.Datas.forEach(item => {
            this.dataList.push({
              imgSrc: defaultImg,
              name: item["CourseName"],
              createTime: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              remark: formatDate(item["CreateTime"], "yyyy-MM-dd"),
              id: item["CourseID"]
            });
          })
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    courseClick (item) {
      this.$store.commit("SET_SUBJECT", item);
      this.$store.commit("SET_SUBJECT_ID", item.id);
      this.$router.push("/subject/view");
    },
    toEdit (item) {
      this.$router.push("/subject/edit/" + item.id + "/" + item.name);
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../styles/common/base";
.subject_list {
  .search_input {
    .van-search__content {
      background: #ebf0ef;
    }
  }

  .van-icon-edit {
    color: #3AA0E0;
  }
}
</style>
